import Vue from 'vue'
import Router from 'vue-router'
import { resolve } from 'q';
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/Index',
    },
    {
      path: '/',
      component: resolve => require(['../components/common/Home.vue'], resolve),
      children: [
        {
          path: '/Index',
          component: resolve => require(['../components/page/Index.vue'], resolve),
          meta: { title: '首页' }
        },
        {
          path: '/Gift',
          component: resolve => require(['../components/page/Gift.vue'], resolve),
          meta: { title: '礼包' }
        },
        {
          path:'/Games',
          component: resolve => require(['../components/page/Games.vue'], resolve),
          meta: { title: '赛事游戏' }
        },
        {
          path: "/Shoplist",
          component: resolve => require(['../components/page/Shoplist.vue'], resolve),
          meta: { title: '商品' }
        },
        {
          path: '/My',
          component: resolve => require(['../components/page/My.vue'], resolve),
          meta: { title: "我的" }
        }
      ]
    },
    {
      path: '/login',
      component: resolve => require(['../components/page/Login.vue'], resolve)
    }
  ]
})



